// colors
[data-dark="false"] {
  --primary: #0795d9;
  --secondary: #7dd3fc;
  --text: #000000;
  --background: #ffffff;
  --background-alt: #fafafa;
  --light-gray: #e0e0e0;
  --gray: #808080;
  --dark-gray: #404040;
  --overlay: #00000020;
}
[data-dark="true"] {
  --primary: #0795d9;
  --secondary: #7dd3fc;
  --text: #000000;
  --background: #ffffff;
  --background-alt: #fafafa;
  --light-gray: #e0e0e0;
  --gray: #808080;
  --dark-gray: #404040;
  --overlay: #00000020;
}

:root {
  // font families
  --title: "Funnel", sans-serif;
  --heading: "Funnel", sans-serif;
  --body: "Funnel", sans-serif;
  --code: "Roboto Mono", monospace;

  // font sizes
  --large: 1.6rem;
  --xl: 1.8rem;
  --xxl: 2.0rem;

  // font weights
  --thin: 300;
  --regular: 500;
  --semi-bold: 600;
  --bold: 700;

  // text line spacing
  --spacing: 1.5;
  --compact: 1.2;

  // effects
  --rounded: 3px;
  --shadow: 0 0 10px 0 var(--overlay);
  --transition: 0.2s ease;
}
